/**
 * JS
 *
 *  Materialize CSS
 *
 * Created by aaric on 2017/7/9.
 */

$(document).ready(function () {
    let $captchaButton = $("#getPhoneCaptcha");

    $captchaButton.click(function () {
        $captchaButton.addClass("disabled");
        $captchaButton.text("");

        $.ajax("/api/captcha/phone", {
            type: "POST",
            data: {
                phone: $("#phone").val()
            },
            success: function () {
                Materialize.toast("", 3000);
                let duration = 60;

                let timer = setInterval(function () {
                    $captchaButton.text(`${duration}`);
                    duration--;
                    if (duration === 0) {
                        clearInterval(timer);
                        $captchaButton.text("");
                        $captchaButton.removeClass("disabled");
                    }
                }, 1000);
            },
            error: function (requset) {
                $captchaButton.text("");

                Materialize.toast(`: ${requset.responseText}`, 3000, "", function () {
                    $captchaButton.removeClass("disabled");
                });
            }
        });
    });
});



